<template>
  <div>
    <SavedGame v-if="saved" :getSaved="getSaved" />
    <div v-else>
      <Loader v-show="showLoader" /> 
      <GameMode v-if="loaded" :mode="'game'" :geojson="geojson" :mapOptions="options" :stopLoader="stopLoader" />
    </div>
  </div>
</template>
<script>
import SavedGame from '../modals/SavedGame'
import GameMode from '../components/GameMode'
import Loader from '../components/Loader'

export default {
  data () {
    return {
      geojson: [],
      options: {
        zoom: 2,
        center: [40, 0],
        maxBounds: [[90, -180], [-60, 225]]
      },
      saved: false,
      loaded: false,
      showLoader: true,
    }
  },
  created () {
    if (localStorage.getItem('guessed') && !this.$route.params.sort) {
      this.saved = true
      return
    }
    this.getContinent()
  },
  methods: {
    getContinent () {
      this.$http.get(`../json/full.json`)
        .then(response => {          
          if (this.$route.params.sort) {
            for (let i = 0; i < response.body.features.length; i++) {
              if (response.body.features[i].properties.pop_est && response.body.features[i].properties.pop_est > this.$route.params.sort) {
                this.geojson.push(response.body.features[i])
              }
            }
          } else {
            this.geojson = [...response.body.features]
          }
          this.loaded = true
        }, response => {
          console.log(response)
      })
    },
    stopLoader () {
      this.showLoader = false
    },
    getSaved () {
      this.saved = false
      this.getContinent()
    }
  },
  components: {
    SavedGame,
    GameMode,
    Loader
  }
}
</script>


