<template>
  <div>
    <Loader v-show="!loaded" />
    <DetailsModal v-if="isClicked" :country="selectedCountry" :close="closeModal" />
    <div class="learn_notification">
      <div class="selectors" :class="openNotification ? 'opened' : ''">
        <select @change="sortBy('pop_est')" v-model="sort.pop_est">
          <option value="" selected disabled hidden>Population</option>
          <option value="all">All</option>
          <option value="-1000000">Less 1 m</option>
          <option value="-2000000">Less 2 m</option>
          <option value="-5000000">Less 5 m</option>
          <option value="5000000">More 5 m</option>
          <option value="10000000">More 10 m</option>
          <option value="50000000">More 50 m</option>
          <option value="100000000">More 100 m</option>
          <option value="min">Min</option>
          <option value="max">Max</option>
        </select>
        <select @change="sortBy('gdp')" v-model="sort.gdp">
          <option value="" selected disabled hidden>GDP</option>
          <option value="all">All</option>
          <option value="-1000">Less 1 k</option>
          <option value="-2000">Less 2 k</option>
          <option value="-5000">Less 5 k</option>
          <option value="5000">More 5 k</option>
          <option value="10000">More 10 k</option>
          <option value="20000">More 20 k</option>
          <option value="min">Min</option>
          <option value="max">Max</option>
        </select>
        <select @change="sortBy('expectancy')" v-model="sort.expectancy">
          <option value="" selected disabled hidden>Life</option>
          <option value="all">All</option>
          <option value="-40">Less 40 years</option>
          <option value="-50">Less 50 years</option>
          <option value="50">More 50 years</option>
          <option value="60">More 60 years</option>
          <option value="70">More 70 years</option>
          <option value="min">Min</option>
          <option value="max">Max</option>
        </select>
        <select @change="sortBy('density')" v-model="sort.density">
          <option value="" selected disabled hidden>Density</option>
          <option value="all">All</option>
          <option value="-10">Less 10</option>
          <option value="-50">Less 50</option>
          <option value="-100">Less 100</option>
          <option value="100">More 100</option>
          <option value="200">More 200</option>
          <option value="min">Min</option>
          <option value="max">Max</option>
        </select>
        <form @submit.prevent="search" class="search">
          <div v-if="error" class="error">no such country</div>
          <label class="search__label"></label>
          <input type="text" v-model="searchCountry" placeholder="Country" @input="checkError">
          <input type="submit" value="Search" class="btn"> 
        </form>
        <div v-if="!isMobile" class="count">Amount - {{ sortedGeojson.length - 1 }}</div>
      </div>
      <div class="opener" @click="openNotification = !openNotification">
        <img src="../assets/images/down.svg" width="16" height="16" :class="openNotification ? 'rotate' : ''">
      </div>
    </div>
    <div id="map" v-if="loaded">    
      <v-map ref="map" :minZoom="2" :options="options.map" class="fullHeight">
        <HomeButton />
        <v-geojson :geojson="sortedGeojson" :options="options.geojson"></v-geojson>
      </v-map>    
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
import Vue2Leaflet from 'vue2-leaflet'
import Loader from './Loader'
import DetailsModal from '../modals/DetailsModal'
import HomeButton from './HomeButton'

export default {
  data () {
    return {
      geojson: [],
      layers: [],
      sortedGeojson: [],  
      options: {
        map: {
          zoom: 2,
          center: [0, 0],
          maxBounds: [[90, -180], [-70, 225]]
        },
        geojson: {
          stroke: true,
          fill: true,
          fillColor: 'rgba(255,255,255,0.5)',
          color: '#000',
          fillOpacity: 1,
          onEachFeature: this.onEachFeature
        }
      },
      loaded: false,
      isClicked: false,
      selectedCountry: {},
      sort: {
        pop_est: '',
        gdp: '',
        expectancy: '',
        density: ''
      },
      searchCountry: '',
      error: false,
      openNotification: false,
      isMobile: false
    }
  },
  created () {
    this.createGeo()
    this.handler()
  },
  methods: {
    createGeo () {
      this.$http.get(`../json/map.json`)
        .then((response) => {
          this.geojson = [...response.body.features]
          this.$http.get(`../json/custom.json`)
            .then(response => {
              this.geojson = [this.geojson[0], ...response.body.features]
              this.sortedGeojson = [...this.geojson]
              this.loaded = true
            }, response => {
              console.log(response)
          })
        }, response => {
          console.log(response)
      })
    },
    onEachFeature (feature, layer) {
      if (layer.feature.properties.name === 'world') {
        layer.setStyle({ stroke: false })
        return
      }
      this.layers.push(layer)
      layer.setStyle({ fillColor: this.randomColor() })
      layer.on('click', () => {
        this.selectedCountry = layer.feature.properties
        this.isClicked = true
      })
    },
    randomColor () {
      return `rgb(${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)})`
    },
    closeModal () {
      this.isClicked = false
    },
    sortBy (type) {
      this.sortedGeojson = [this.geojson[0]]
      for (let key in this.sort) {
        if (key !== type) {
          this.sort[key] = ''
        }
      }
      if (this.sort[type] === 'all') {
        this.sortedGeojson = [...this.geojson]
      } else if (this.sort[type] === 'max') {
        let max = Math.max.apply(Math, this.geojson.map((o) => {
          return o.properties[type] ? +o.properties[type] : 0
        }))
        this.sortedGeojson.push(this.geojson.find((o) => {
          return +o.properties[type] == max
        }))
      } else if (this.sort[type] === 'min') {
        let min = Math.min.apply(Math, this.geojson.map((o) => {
          return o.properties[type] ? +o.properties[type] : 9999999
        }))
        this.sortedGeojson.push(this.geojson.find((o) => {
          return +o.properties[type] == min
        }))
      } else if (Math.sign(this.sort[type]) === -1) {
        for (let i = 0; i < this.geojson.length; i++) {
          if (+this.geojson[i].properties[type] < Math.abs(this.sort[type])) {
            this.sortedGeojson.push(this.geojson[i])
          }
        }
      } else {
        for (let i = 0; i < this.geojson.length; i++) {
          if (+this.geojson[i].properties[type] > this.sort[type]) {
            this.sortedGeojson.push(this.geojson[i])
          }
        }
      }
    },
    search () {
      for (let i = 0; i < this.layers.length; i++) {
        if (this.layers[i].feature.properties.name.toLowerCase() === this.searchCountry.toLowerCase()) {
          this.error = false
          this.layers[i].fire('click')
          break
        } else if (i === this.layers.length - 1) {
          this.error = true
        }
      }
    },
    checkError () {
      if (this.error) {
        this.error = false
      }
    },
    handler () {
      const WIDTH = 768
      const RATIO = 3
      const { body } = document
      if (!document.hidden) {
        const rect = body.getBoundingClientRect()
        this.isMobile = rect.width - RATIO < WIDTH
      }
    }
  },
  components: {
    'v-map': Vue2Leaflet.Map,
    'v-geojson': Vue2Leaflet.GeoJSON,
    Loader,
    DetailsModal,
    HomeButton
  },
}
</script>
<style scoped>
.learn_notification {
  position: absolute;
  width: 100%;
  font-size: 20px;
  z-index: 900;
}
select, input {
  width: 150px;
  margin: 0 10px;
  padding: 5px 10px;
  border: 1px solid #2c3e50;
  outline: 0;
  border-radius: 5px;
  font: 15px 'Montserrat';
  transition: all 0.3s;
}
.search {
  position: relative;
}
.search__label {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 20px;
  height: 20px;
  background-image: url('../assets/images/search.svg')
}
input {
  margin: 0;
}
input[type="text"] {
  padding: 5px 10px 5px 30px;
}
input[type="submit"] {
  width: auto;
  height: auto;
  margin-left: 5px;
  background: #51667f;
  color: #fff;
}
input[type="submit"]:hover {
  background-color: #354354;
}
select:focus, input:focus {
  box-shadow: 0 0 10px #2c3e50;
}
form {
  position: relative;
}
.error {
  position: absolute;
  top: -20px;
  padding: 1px;
  background-color: tomato;
  color: #fff;
  font-size: 14px;
  opacity: 0;
  animation: error 0.5s forwards;
}
@keyframes error {
  100% {
    opacity: 1;
  }
}

.selectors {
  overflow: hidden;
  transition: all 0.5s;
  height:0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: #9bffe6;
}
.opened {
  height:auto;
  padding: 20px;
}
.opener {
  position:absolute;
  bottom: -30px;
  width: 100%;
  height: 30px;
  line-height: 30px;
  background-color: #51667f;
  cursor: pointer;
}
img {
  transform: rotateX(0);
  transition: all 0.5s;
}
.rotate {
  transform: rotateX(180deg);
}
.count {
  margin-left: 20px;
}
</style>


