<template>
  <div>
    <Loader v-show="!loaded" />
    <div class="learn_notification">
      Choose Country
    </div>
    <div id="map" v-if="loaded">    
      <v-map ref="map" :minZoom="2" :options="options.map" class="fullHeight">
        <HomeButton />
        <v-geojson :geojson="geojson" :options="options.geojson"></v-geojson>
      </v-map>    
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
import Vue2Leaflet from 'vue2-leaflet'
import router from '../router'
import Loader from './Loader'
import HomeButton from './HomeButton'

export default {
  data () {
    return {
      geojson: [],
      options: {
        map: {
          zoom: 2,
          center: [0, 0],
          maxBounds: [[90, -180], [-60, 225]]
        },
        geojson: {
          stroke: true,
          fill: true,
          strokeColor: '#000',
          color: '#000',
          fillOpacity: 1,
          onEachFeature: this.onEachFeature
        }
      },
      loaded: false
    }
  },
  created () {
    this.createGeo()
  },
  methods: {
    createGeo () {
      this.$http.get(`../json/region.json`)
        .then(response => {
          this.geojson = [...response.body.features]
          this.loaded = true
        }, response => {
          console.log(response)
      })
    },
    onEachFeature (feature, layer) {
      layer.setStyle({ fillColor: this.randomColor() })
      layer.on('click', () => {
        this.changeRoute(layer)
      })
    },
    changeRoute (layer) {
      router.push({ 
        name: 'Subject',
        params: { 
          name: layer.feature.properties.name.replace(/ /g, '').toLowerCase(),
          country: true,
          latlng: {
            center: layer.getCenter(),
            maxBounds: [
            layer._bounds.getNorthEast(),
            layer._bounds.getSouthWest()
            ],
            zoom: 4
          }
        }
      })
    },
    randomColor () {
      return `rgb(${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)})`
    }
  },
  components: {
    'v-map': Vue2Leaflet.Map,
    'v-geojson': Vue2Leaflet.GeoJSON,
    Loader,
    HomeButton
  },
}
</script>
<style scoped>
.chosen {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7);  
}
.chosen_object {
  font-size: 100px;
  color: #fff;  
}
.learn_notification {
  position: absolute;
  right: 50px;
  top: 50px;
  font-size: 32px;
  color: #fff;
  z-index: 900;
}
@media screen and (max-width: 400px) {
  .learn_notification {
    right: 0;
    top: 10px;
    width: 100%;
    font-size: 20px;
  }
}
</style>


