<template>
  <div>
    <Loader v-show="!loaded" />
    <OnlineModal v-if="enemyLeft || (loaded && game.count === geojson.length)" :reason="reason" :nickname="nickname" :enemy="enemy" :score="game.score" :colors="sideColors" />
    <div class="modal-backdrop" v-if="loaded && enemyTurn"></div>
    <div v-if="loaded" id="map">
      <div v-if="game.count !== geojson.length">
        <div v-if="!enemyTurn" class="notification" :class="danger ? 'danger': ''">
          <p class="time"><span>Time: {{ seconds }}</span></p>
          <p class="attempts"><span>Attempts: {{ game.attempts }}</span></p>
          <div class="text--big" v-if="game.count !== geojson.length">
            {{ countries[this.game.count] }}
          </div>
          <ul class="users">
            <li v-for="(user, id) in users" :key="id" class="user__score" :class="user === nickname ? 'mySide' : ''">
              <span>{{ user }}</span>
              <span v-if="user === nickname">{{ game.score.my }}</span>
              <span v-else>{{ game.score.enemy }}</span>
            </li>
          </ul>
        </div>
        <div v-else class="notification">
          <div class="text--big enemyTurn">Opponent's Turn<span>.</span><span>.</span><span>.</span></div>
        </div>
      </div>
      <v-map ref="map" :minZoom="2" :options="options.map" class="fullHeight">
        <HomeButton />
        <v-geojson :geojson="geojson" :options="options.geojson"></v-geojson>
      </v-map>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
import Vue2Leaflet from 'vue2-leaflet'
import VueSocketio from 'vue-socket.io'
import Loader from '../components/Loader'
import HomeButton from '../components/HomeButton'
import OnlineModal from '../modals/OnlineModal'

Vue.use(VueSocketio, '192.168.88.10:5000')

export default {
  data () {
    return {
      geojson: [],
      layers: [],
      countries: [],
      options: {
        map: {
          zoom: 2,
          center: [0, 0],
          maxBounds: [[90, -180], [-60, 225]]
        },
        geojson: {
          stroke: true,
          fill: true,
          strokeColor: '#000',
          color: '#000',
          fillColor: '#fff',
          fillOpacity: 1,
          onEachFeature: this.onEachFeature
        }
      },
      game: {
        count: 0,
        attempts: 5,
        score: {
          my: 0,
          enemy: 0
        },
      },
      loaded: false,
      seconds: 10,
      interval: null,
      danger: false,
      nickname: '',
      users: [],
      sideColors: {
        my: '',
        enemy: ''
      },
      enemyTurn: true,
      enemyLeft: false,
      reason: ''
    }
  },
  computed: {
    enemy () {
      return this.users.filter((item) => item !== this.nickname).toString()
    }
  },
  created () {
    if (this.getCookie('name')) {
      this.nickname = JSON.parse(this.getCookie('name')).name
      this.$socket.emit('sendName', this.nickname)
    }
    this.getContinent()
  },
  beforeRouteLeave (to, from, next) {
    if (this.game.count === this.geojson.length) {
      next()
      return
    }
    this.$socket.emit('userLeft')
    next()
  },
  methods: {
    getContinent () {
      this.$http.get(`../json/full.json`)
        .then(response => {         
          this.geojson = [...response.body.features]
        }, response => {
          console.log(response)
      })
    },
    onEachFeature (feature, layer) {
      this.layers.push(layer)
      layer.bindPopup(layer.feature.properties.name)
      layer.on('click', () => {
        this.show(layer)
      }, this)
    },
    show (layer) {
      if (layer.feature.properties.name === this.countries[this.game.count]) {
        for (let i = 0; i < this.layers.length; i++) {
          if (this.layers[i].feature.properties.name === this.countries[this.game.count]) {
            this.layers[i].setStyle({ fillColor: this.sideColors.my })
            this.layers[i].off('click') 
            break
          }
        }
        this.$socket.emit('countryClick', {[this.nickname]: true})
        this.game.score.my++
        this.enemyTurn = true
        this.resetData()
      } else {
        this.game.attempts--
        if (this.game.attempts === 0) {
          this.$socket.emit('countryClick', {[this.nickname]: false})
          this.enemyTurn = true
          this.resetData()
        }
      }
    },
    resetData () {
      clearInterval(this.interval)
      this.game.count++
      if (this.game.count === this.geojson.length) {
        this.$socket.emit('gameResult')
        return
      }
      this.game.attempts = 5
      this.seconds = 10
      this.danger = false
    },
    getCookie (name) {
      let matches = document.cookie.match(new RegExp("(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"))
      return matches ? decodeURIComponent(matches[1]) : undefined
    },
    makeInterval () {
      this.interval = setInterval(() => {
        this.seconds--
        if (this.seconds === 3) {
          this.danger = true
        }
        if (this.seconds === 0) {
          this.$socket.emit('countryClick', {[this.nickname]: false})
          this.enemyTurn = true
          this.resetData()
        }
      }, 1000)
    }
  },
  sockets: {
    startGame (data) {
      this.users = [...data.users]
      this.countries = [...data.countries]
      this.loaded = true
      if (this.nickname === this.users[0]) {
        this.sideColors.my = 'blue'
        this.sideColors.enemy = 'tomato'
        this.enemyTurn = false
        this.makeInterval()
      } else {
        this.sideColors.enemy = 'blue'
        this.sideColors.my = 'tomato'
      }
    },
    checkAnswer (data) {
      if (Object.keys(data)[0] !== this.nickname) {
        if (data[Object.keys(data)[0]]) {
          for (let i = 0; i < this.layers.length; i++) {
            if (this.layers[i].feature.properties.name === this.countries[this.game.count]) {
              this.layers[i].setStyle({ fillColor: this.sideColors.enemy })
              this.layers[i].off('click')
              break
            }
          }
          this.game.score.enemy++
        }
        this.resetData()
        if (this.game.count === this.geojson.length) {
          return
        }
        this.enemyTurn = false
        this.makeInterval()
      }
    },
    endMatch () {
      this.reason = 'leave'
      this.enemyLeft = true
      clearInterval(this.interval)
    }
  },
  components: {
    'v-map': Vue2Leaflet.Map,
    'v-geojson': Vue2Leaflet.GeoJSON,
    Loader,
    HomeButton,
    OnlineModal
  }
}
</script>
<style scoped>
.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
}
.notification {
  position: absolute;
  right: 50px;
  top: 50px;
  width: 300px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #404040;
  background-color: #fff;
  box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.3);
  z-index: 1000;
}
.time, .attempts {
  position: absolute;
  top: 5px;
  margin: 0;
  font-size: 14px;
}
.time {
  left: 5px;
}
.attempts {
  right: 5px;
}
.text--big {
  font-size: 22px;
}
@media screen and (max-width: 400px) {
  .notification {
    right: 0;
    top: 0;
    width: 100%;
  }
}
.danger {
  animation: danger 1s infinite;
}
.users {  
  width: 100%;
  padding: 0;
  list-style-type: none;
}
.user__score {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px;
  color: #fff;
}
.user__score:nth-child(1) {
  background-color: rgba(0, 0, 255, 0.5);
}
.user__score:nth-child(1).mySide {
  box-shadow: 0 0 10px blue;
}
.user__score:nth-child(2) {
  background-color: rgba(255, 99, 71, 0.5);
}
.user__score:nth-child(2).mySide {
  box-shadow: 0 0 10px tomato;
}
@keyframes danger {
  50% {
    box-shadow: 0 0 20px 5px inset tomato;
  }
}
.enemyTurn span {
  animation-name: blink;
  animation-duration: 1.4s;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
}
.enemyTurn span:nth-child(2) {
  animation-delay: .2s;
}
.enemyTurn span:nth-child(3) {
  animation-delay: .4s;
}
@keyframes blink {
  0% {
    opacity: .2;
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: .2;
  }
}
</style>


