<template>
  <div id="map">
    <Modal v-if="game.count === states.length" :mode="mode" :reason="'win'" :score="game.score" />
    <div v-else class="learn_notification" :class="animation ? 'animation' : ''" @animationend="endAnime" @webkitAnimationEnd="endAnime" @msAnimationEnd="endAnime" @mozAnimationEnd="endAnime">
      {{ states[game.count] }}
      <p><span class="bold">{{ game.count }}</span> of {{ states.length }}</p>
      <div class="search">
        <label class="search__label"></label>
        <input tye="text" v-model="searchCountry" placeholder="Search" @input="search">
      </div>
      <div v-if="foundCountries.length > 0" class="found">
        <div v-for="(country, id) in foundCountries" :key="id" class="found__country" @click="zoomCountry(country)">{{ country }}</div>
      </div>
    </div>
    <v-map ref="map" :minZoom="2" :options="mapOptions" class="fullHeight">
      <HomeButton />
      <v-geojson :geojson="geojson" :options="options.geojson"></v-geojson>
    </v-map>
  </div>  
</template>
<script>
import Vue2Leaflet from 'vue2-leaflet'
import Modal from '../modals/Modal'
import HomeButton from './HomeButton'

export default {
  data () {
    return {
      options: {
        geojson: {
          stroke: true,
          fill: true,
          strokeColor: '#000',
          color: '#000',
          fillColor: '#fff',
          fillOpacity: 1,
          onEachFeature: this.onEachFeature
        }
      },
      states: [],
      guessed: [],
      game: {
        count: 0,
        attempts: 5,
      },
      layers: [],
      animation: false,
      searchCountry: '',
      foundCountries: [],
      isMobile: false
    }
  },
  created () {
    for (let i = 0; i < this.geojson.length; i++) {
      this.states.push(this.geojson[i].properties.name)
    }
    this.states.sort(this.compareRandom)
    this.handler()
  },
  mounted () {
    this.stopLoader()
  },
  props: {
    mode: {
      type: String,
      required: true
    },
    geojson: {
      required: true
    },
    mapOptions: {
      type: Object,
      required: true
    },
    stopLoader: {
      type: Function
    },
    latlng: {
      type: Number
    },
    country: {
      type: Boolean
    }
  },
  methods: {
    onEachFeature (feature, layer) {
      layer.bindPopup(layer.feature.properties.name + (layer.feature.properties.capital ? '<br>Capital - ' + layer.feature.properties.capital : ''))
      this.layers.push(layer)
      layer.on('click', () => {
        this.show(layer)
      }, this)
    },
    show (layer) {
      if (layer.feature.properties.name === this.states[this.game.count]) {
        this.animation = true
        this.game.count++
        this.game.attempts = 5
        this.guessed.push(layer.feature.properties.name)
        layer.setStyle({ fillColor: this.randomColor() })
        layer.getElement().classList.remove('guess_me')
        layer.off('click')
        this.searchCountry = ''
        this.search()
      } else {
        this.game.attempts--
        if (this.game.attempts === 0) {
          this.zoomCountry(this.states[this.game.count], true)          
        }
      }
    },
    compareRandom () {
      return Math.random() - 0.5
    },
    randomColor () {
      return `rgb(${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)})`
    },
    endAnime () {
      this.animation = false
    },
    search () {
      this.foundCountries = []
      for (let i = 0; i < this.layers.length; i++) {
        if (this.layers[i].feature.properties.name.toLowerCase().startsWith(this.searchCountry.toLowerCase()) && this.searchCountry.length > 0) {
          if (!this.guessed.includes(this.layers[i].feature.properties.name)) {
            this.layers[i].setStyle({ fillColor: this.randomColor() })
          }
          this.foundCountries.push(this.layers[i].feature.properties.name)
        } else if (!this.guessed.includes(this.layers[i].feature.properties.name)) {
          this.layers[i].setStyle({ fillColor: '#fff' })
        }
      }
    },
    zoomCountry (country, mode) {
      for (let i = 0; i < this.layers.length; i++) {
        if (this.layers[i].feature.properties.name === country) {
          if (mode) {
            this.layers[i].setStyle({ fillColor: '#9BFFE6' })
            this.layers[i].getElement().classList.add('guess_me')
          }
          this.$refs.map.mapObject.flyTo({
            lat: ((this.layers[i]._bounds._northEast.lat + this.layers[i]._bounds._southWest.lat) / 2),
            lng: ((this.layers[i]._bounds._northEast.lng + this.layers[i]._bounds._southWest.lng) /2)
          }, 6)
          break
        }
      }
      if (this.isMobile) {
        this.searchCountry = ''
        this.foundCountries = []
      }
    },
    handler () {
      const WIDTH = 768
      const RATIO = 3
      const { body } = document
      if (!document.hidden) {
        const rect = body.getBoundingClientRect()
        this.isMobile = rect.width - RATIO < WIDTH
      }
    }
  },
  components: {
    'v-map': Vue2Leaflet.Map,
    'v-geojson': Vue2Leaflet.GeoJSON,
    Modal,
    HomeButton
  }
}
</script>
<style scoped>
.learn_notification {
  position: absolute;
  right: 50px;
  top: 50px;
  width: 300px;
  padding: 20px 29px 0; 
  color: #404040;
  background-color: #fff;
  box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.3);
  z-index: 900;
  font-size: 22px;
}
.learn_notification p {
  position: absolute;
  top: 5px;
  right: 5px;
  margin: 0;
  font-size: 14px;
}
@media screen and (max-width: 400px) {
  .learn_notification {
    right: 0;
    top: 0;
    width: 100%;
  }
}
.animation {
  animation-name: next;
  animation-duration: 0.5s;
  animation-timing-function: ease-in-out;
}
.country {
  padding: 10px 10px 20px 10px;
  font-size: 20px;
}
@keyframes next {
  50% {
    transform: translateX(400px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
.search {
  position: relative;
}
.search__label {
  position: absolute;
  top: 15px;
  left: 25px;
  width: 20px;
  height: 20px;
  background-image: url('../assets/images/search.svg')
}
input {
  width: 200px;
  margin: 10px 0;
  padding: 5px 10px 5px 30px;
  border: 1px solid #2c3e50;
  outline: 0;
  border-radius: 5px;
  font: 15px 'Montserrat';
  transition: all 0.3s;
}
input:focus {
  box-shadow: 0 0 10px #2c3e50;
}
.found {
  position: absolute;
  left: 0;
  right: 0;
  max-height: 300px;
  overflow-y: auto;
  background-color: #fff;
  border: 1px solid #51667f;
}
.found__country {
  padding: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}
.found__country:hover {
  background-color: #9bffe6;  
}
</style>


