import Vue from 'vue'
import App from './App.vue'
import router from './router'
import VueResource from 'vue-resource'
import VueFire from 'vuefire'

Vue.use(VueFire)
Vue.use(VueResource)

Vue.router = router
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')



