<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app'
}
</script>

<style>
@import "~leaflet/dist/leaflet.css";

* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
#app {
  min-height: 100vh;
  font-family: 'Montserrat', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.btn {
  position: relative;
  width: 200px;
  height: 50px;
  line-height: 1;
  background: transparent;
  color: #2c3e50;
  border: 1px solid #2c3e50;
  border-radius: 5px;  
  font-family: 'Michroma';
  font-size: 16px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 2px;
  cursor: pointer;
  outline: none;
  overflow: hidden;
  transition: all 0.2s ease;
}
.btn:hover {
  background: #51667f;
  color: #fff;
}
.btn:active {
  transform: translate(0px, 5px);
  -webkit-transform: translate(0px, 5px);
}
.btn--start {
  background: linear-gradient(to bottom, transparent, black);
  color: #fff;
  border-color: #fff;
}
.btn--small {
  width: 150px;
}
.btn--sign {
  width: 100px;
  background-color: #fff;
  color: #2c3e50;
  font-size: 13px;
}
.fullHeight {
  min-height: 100vh;
}
.leaflet-container {
  background-color: #A3CCFF;
}
.leaflet-popup-content {
  text-align: center;
  font-size: 16px;
}
.bold {
  font-weight: bold;
  color: #107bff;
}
.guess_me {
  animation: guess 2s infinite;
}
@keyframes guess {
  0% {
    fill-opacity: 1;
  }
  25% {
    fill-opacity: 0;
  }
  50% {
    fill-opacity: 1;
  }
  75% {
    fill-opacity: 0
  }
  100% {
    fill-opacity: 1;
  }
}
</style>



