<template>
  <div>
    <div class="brand">
      <h1 class="brand__name">Worldaria</h1>
      <div v-if="!authUser" class="sign">
        <div class="error" v-if="error.smallName">* name - more 2 letters</div>
        <div class="error" v-else-if="error.longName">* name - less 20 letters</div>
        <form @keypress.prevent.13="signIn">
          <input type="text" class="signIn" v-model="nickname" placeholder="Nickname" @input="inputName">
          <button class="btn btn--sign" @click="signIn">Sign in</button>
        </form>
      </div>
      <div v-else class="sign">
        <span class="nickname" :class="animationNickname ? 'animationNickname' : ''">{{ nickname }}</span>
        <button class="btn btn--sign" @click="signOut">Sign out</button>
      </div>
    </div>
    <div class="start">
      <div class="left">
        <p>In Game mode you have 5 attempts for each country. If you mark right, you will need to guess the capital of that country. Each right country and capital will give you 1 point. After 5 wrong attempts you will lose 1 point</p>
        <router-link to="/game" tag="button" class="btn btn--start">Play</router-link>
        <p>In Hard mode you have only 10 seconds on each country</p>
        <router-link to="/hard" tag="button" class="btn btn--start">Hard</router-link>
        <p>In Online mode you will play with real person</p>
        <div class="btn--online">
          <div class="error error--sign" v-if="error.signIn">* sign in to play online</div>
          <router-link to="/online" tag="button" class="btn btn--start">Online</router-link>
        </div>
      </div>
      <div class="right">
        <p class="mb0">Play countries by their population</p>
        <div class="buttons">
          <router-link :to="{ name: 'Game', params: { sort: 5000000 }}" tag="button" class="btn btn--start btn--small">> 5 m</router-link>
          <router-link :to="{ name: 'Game', params: { sort: 10000000 }}" tag="button" class="btn btn--start btn--small">> 10 m</router-link>
          <router-link :to="{ name: 'Game', params: { sort: 50000000 }}" tag="button" class="btn btn--start btn--small">> 50 m</router-link>
        </div>
        <p>In Learn mode you have unlimited attempts. After 5 wrong attempts the right country will be accentuated</p>
        <router-link to="/learn/full" tag="button" class="btn btn--start">Learn All</router-link>
        <div class="buttons">
          <router-link to="/learn/continent" tag="button" class="btn btn--start">Continents</router-link>
          <router-link to="/learn/countries" tag="button" class="btn btn--start">Regions</router-link>
          <router-link to ="/learn/details" tag="button" class="btn btn--start">Details</router-link>
          <router-link to ="/learn/history" tag="button" class="btn btn--start">History</router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import md5 from 'md5'
import { database } from '../config'
let USERS = database.ref('users')

export default {
  data () {
    return {
      nickname: '',
      authUser: false,
      error: {
        smallName: false,
        longName: false,
        signIn: false
      },
      animationNickname: false
    }
  },
  beforeCreate () {
    document.body.className = 'start';
  },
  created () {
    if (this.getCookie('name')) {
      this.authUser = true
      this.nickname = JSON.parse(this.getCookie('name')).name
    }    
  },
  beforeRouteLeave (to, from, next) {
    if (to.path === '/online') {
      if (!this.nickname) {
        this.error.signIn = true
        return
      }
    }
    next()
  },
  methods: {
    signIn () {
      if (this.nickname.length > 2 && this.nickname.length < 20) {
        this.error.signIn = false
        let date = new Date(new Date().getTime() + 60 * 1000 * 1000)
        USERS.child(md5(this.nickname + date.toUTCString())).set({
          name: this.nickname
        })
        this.authUser = true
        this.animationNickname = true
        this.setCookie(date)
      } else if (this.nickname.length < 3) {
        this.error.smallName = true
      }
    },      
    signOut () {
      this.deleteCookie()
      this.nickname = ''
      this.authUser = false
      this.animationNickname = false
    },
    inputName () {
      if (this.nickname.length > 20) {
        this.nickname = this.nickname.slice(0, -1)
        this.error.longName = true
      } else if (this.error.smallName && this.nickname.length > 2) {
        this.error.smallName = false
      } else {
        this.error.longName = false
      }
    },
    getCookie (name) {
      let matches = document.cookie.match(new RegExp("(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"))
      return matches ? decodeURIComponent(matches[1]) : undefined
    },
    setCookie (date) {
      document.cookie = `name=${ JSON.stringify({ name: this.nickname, hash: md5(this.nickname + date.toUTCString()) }) }; path=/; expires=${date.toUTCString()}`
    },
    deleteCookie () {
      let date = new Date(new Date().getTime() + (-1) * 1000)
      document.cookie = `name=""; path=/; expires=${date.toUTCString()}`
    }
  }
}
</script>
<style scoped>

.start {
  display: flex;
  color: #fff;
}
.brand {
  position: relative;
  height: 70px;
  margin-bottom: 30px;
  text-align: center;
  background-color: rgba(0,0,0,0.5);
  color: #fff;
}
.brand__name {
  margin: 0;
  font-size: 48px;
  font-family: 'Michroma', sans-serif;
  text-transform: uppercase;
  letter-spacing: -6px;
}
@media screen and (max-width: 850px) {
  .brand__name {
    text-align: left;
    padding-left: 20px;
  }
}
.sign {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  align-items: center;
}
.signIn {
  animation: opacityChanger 0.5s;
}
.left, .right {
  width: 50%;
  padding: 20px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 1.6;
  font-size: 20px;
}
.mb0 {
  margin-bottom: 0;
}
.buttons {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.buttons .btn {
  margin-top: 20px;
}
.nickname {
  position: relative;
  margin-right: 20px;
  letter-spacing: normal;
}
.animationNickname {
  animation: nick 1s cubic-bezier(.62,1.83,.79,.72);
}
.error {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 5px;
  font-size: 14px;
  background-color: tomato;
  color: #fff;
  opacity: 0;
  animation: opacityChanger 0.5s forwards;
}
.error--sign {
  top: -22px;
}
input {
  width: 200px;
  height: 50px;
  border-radius: 5px;
  border: 2px solid #7CCCB8;
  padding: 0 20px;
  outline: none;
  font-family: inherit;
  transition: box-shadow 0.3s;
}
input:focus {
  box-shadow: 0px 0px 10px #3E665C inset;
}
.btn--online {
  position: relative;
}
@media screen and (max-width: 650px) {
  .start {
    flex-wrap: wrap;
  }
  .left, .right {
    width: 100%;
    padding: 20px;
    font-size: 18px;
  }
  .right {
    padding: 20px 20px 40px;
  }
  .brand__name {
    font-size: 36px;
  }
}
@keyframes opacityChanger {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes nick {
  0% {
    transform: translateX(-200px);
  }
  100% {
    transform: translateX(0);
  }
}
</style>


